import React, {memo, useCallback, useEffect, useRef} from 'react';
import {
  Fonts,
  goBack,
  styled,
  ToastService,
  useAutoToastError,
  useBoolean,
  useNavigationParams,
} from '@true/core';
import Animated, {
  runOnUI,
  useAnimatedRef,
  useDerivedValue,
  useSharedValue,
} from 'react-native-reanimated';
import {
  AnimatedKeyboardAvoidingViewRef,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  UIInput,
} from '@true/ui';
import {DiscardDialog} from '@/components/DiscardDialog';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import {ICreateCoreValue, initCreateCoreValue} from '@/store/corevalues/type';
import {getCoreValues} from '@/store/corevalues';
import {
  requestCreateCoreValues,
  requestEditCoreValues,
} from '@/store/corevalues/function';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {HeaderCreatePost} from '@/screens/CreatePostScreen/HeaderCreatePost';
import {CheckboxWithText} from '@/components/CheckboxWithText';
import LoadingModal from '@/components/LoadingModal';

export interface CreateCoreValueScreenParams {
  id: number;
}

export const CreateCoreValueScreen = memo(function CreateCoreValueScreen() {
  const {id} = useNavigationParams<CreateCoreValueScreenParams>();
  const params = useSharedValue<ICreateCoreValue>(initCreateCoreValue);
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useAnimatedRef<any>();
  const [visibleDialog, showDialog, hideDialog] = useBoolean();

  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      if (id !== -1) {
        await requestEditCoreValues(params.value);
      } else {
        await requestCreateCoreValues(params.value);
        ToastService.show('Create core values successfully');
      }
      goBack();
    }, [id, params]);
  useAutoToastError(createError);

  const onGoBack = useCallback(() => {
    console.log('aa');
    goBack();
  }, []);

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  const animatedActiveBtn = useDerivedValue(() => {
    return !!params.value.name;
  }, []);

  useEffect(() => {
    let _params = {
      ...initCreateCoreValue,
    };
    if (id > 0) {
      const coreValue = getCoreValues(id);
      _params = {
        id: coreValue?.id,
        content: coreValue?.content || '',
        name: coreValue?.name || '',
        image: coreValue?.image,
        notify: coreValue?.notify,
        notify_email: coreValue?.notify_email,
        disable_comment: coreValue?.disable_comment,
      };
    }
    params.value = _params;
  }, [id]);

  return (
    <ScreenWrapper>
      <MyKeyboardAvoidingView ref={ref} scrollRef={scrollRef}>
        <HeaderCreatePost
          onPress={onPressSubmit}
          active={animatedActiveBtn}
          id={id}
          loading={loading}
          onGoBack={onGoBack}
          title={'Create core values'}
        />
        <LoadingModal isVisible={loading} />
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          <SContainer>
            <SField>
              <UIInput.Text
                title={'Title'}
                keyName={'name'}
                params={params}
                placeholder={'---'}
                defaultValue={''}
                onChangeValue={onChangeValue}
                required
              />
              <UIInput.Text
                title={'Content'}
                keyName={'content'}
                params={params}
                onChangeValue={onChangeValue}
              />
            </SField>
            <SField>
              <STextBold>Advanced settings</STextBold>
              <CheckboxWithText
                params={params}
                keyName={'draft'}
                title={'Save as a draft'}
              />
              <CheckboxWithText
                params={params}
                keyName={'disable_comment'}
                title={'Disable comments'}
              />
              <CheckboxWithText
                params={params}
                keyName={'notify'}
                title={'Send notification'}
              />
              <CheckboxWithText
                params={params}
                keyName={'notify_email'}
                title={'Notify everyone via email'}
              />
            </SField>
            <AnimatedBottomSpace />
          </SContainer>
        </SScrollView>
        <DiscardDialog
          title={'Discard change'}
          content={
            'You have made changes to this item that have not been saved. If you leave this page, your changes will be lost.'
          }
          visible={visibleDialog}
          onClose={hideDialog}
        />
      </MyKeyboardAvoidingView>
    </ScreenWrapper>
  );
});
const SContainer = styled(Animated.View)`
  flex: 1;
  padding: 12px 16px;
  gap: 12px;
`;

const SField = styled.View`
  background-color: ${p => p.theme.white};
  gap: 12px;
`;

const STextBold = styled.Text`
  font-family: ${Fonts.Medium};
  font-size: 16px;
  line-height: 24px;
  color: ${p => p.theme.neutral1};
`;
const SScrollView = styled.ScrollView`
  flex: 1;
  background-color: white;
  margin-top: 8px;
`;
